import { chunk } from "./arrayUtil";
export function validateJoystick(joystick) {
    return true;
}
const colors = {
    PK: '#FF69B4',
    SR: '#C0C0C0',
    GD: '#CFB53B',
    BK: '#000000',
    BN: '#964B00',
    RD: '#FF0000',
    OG: '#FFA500',
    OR: '#FFA500',
    YE: '#FFFF00',
    GN: '#9ACD32',
    GR: '#C0F0C0',
    BU: '#6495ED',
    VT: '#9400D3',
    GY: '#A0A0A0',
    WH: '#FFFFFF',
};
const parseColors = (color) => {
    const cParts = chunk(color.split(''), 2).map(c => c.reduce((a, c) => a.concat(c)), []); //
    const result = cParts.filter(c => colors[c] !== undefined);
    return result;
};
export function cableColorToBackgroundColor(cableTraceColor) {
    const colorArr = parseColors(cableTraceColor);
    const firstColor = colors[colorArr[0]];
    if (colorArr.length > 1) {
        const secondColor = colors[colorArr[1]];
        return `repeating-linear-gradient(-45deg, ${firstColor} 0 5px, ${secondColor} 5px 10px)`;
    }
    else {
        return firstColor;
    }
}
