import commentGrammar from "./comment.pegjs";
import grammar from "./grammar.pegjs";
import * as PEG from 'pegjs';
const commentParser = PEG.generate(commentGrammar);
const _parser = PEG.generate(grammar);
function parser(input) {
    const commentResult = commentParser.parse(input);
    const code = commentResult.join("");
    return _parser.parse(code);
}
export default parser;
