import { compose, combineReducers, createStore, applyMiddleware } from 'redux';
import createSagaMiddleware from 'redux-saga';
import mainReducer, { saga as mainSaga, actions as mainActions } from "./main/main";
import testReducer, { saga as testSaga, actions as testActions } from './test/test';
import usbReducer, { saga as usbSaga, actions as usbActions } from '../usb/saga';
import { fork, all, takeEvery, put } from 'redux-saga/effects';
import { routerMiddleware, connectRouter, push } from 'connected-react-router';
import { createHashHistory } from 'history';
import Worker from '../workers/test.worker';
import { validBoards, getUSBDevices, getDeviceFilter } from '../usb/webusb';
export const history = createHashHistory(window);
export const rootReducer = combineReducers({
    main: mainReducer,
    test: testReducer,
    usb: usbReducer,
    router: connectRouter(history),
});
const sagaMiddleware = createSagaMiddleware();
const composeEnhancers = // process.env.NODE_ENV !== 'production' ?
 (typeof window === 'object' && window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__'] ?
    window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__']({}) : compose); //: compose
export const store = createStore(rootReducer, composeEnhancers(applyMiddleware(routerMiddleware(history), sagaMiddleware)));
const rootSaga = function* () {
    yield all([
        fork(mainSaga),
        fork(testSaga),
        fork(usbSaga)
    ]);
    yield takeEvery(mainActions.selectTest, function* (action) {
        yield put(push(`/test/${action.config}/${action.serial || ""}`));
        yield put(testActions.initTest.request({ serial: action.serial, config: action.config }));
    });
    yield takeEvery(usbActions.workerRequest, function* (action) {
        usbWorker.postMessage(action.payload);
    });
};
sagaMiddleware.run(rootSaga);
export const usbWorker = new Worker();
usbWorker.addEventListener('message', (event) => {
    const response = event.data;
    switch (response.action) {
        // Open the device specified with vendorId and productId.
        case 'log':
            store.dispatch(usbActions.workerlog(response.payload));
            break;
        case 'error':
            //console.error(response.payload)
            break;
        case 'port-opened':
            store.dispatch(usbActions.connected());
            break;
        case 'port-closed':
            store.dispatch(usbActions.disconnected());
            break;
        case 'adc':
            store.dispatch(usbActions.adc(response.payload));
            break;
        case 'worker-ready':
            console.log('worker-ready');
            getUSBDevices(validBoards)
                .then(devices => {
                if (devices.length == 1) {
                    const device = devices[0];
                    console.log("Found one suitable usb device. Connecting", device);
                    const filter = getDeviceFilter(device);
                    store.dispatch(usbActions.connect(filter));
                }
            });
            break;
    }
});
window.t_port = (ports) => {
    const r = [];
    while (ports.length) {
        r.push(ports.splice(0, 8));
    }
    store.dispatch(usbActions.adc({
        boardId: -1,
        ADC: r.map((v, i) => ({
            chip: i,
            values: v,
        }))
    }));
};
document.onkeydown = (e) => {
    if (e.keyCode === 27) {
        store.dispatch(testActions.abortTest({}));
    }
    if (e.keyCode === 32) {
        store.dispatch(testActions.startTest({}));
    }
};
window.t_mockState = () => {
    store.dispatch(testActions.mockState());
};
