import { createAsyncAction, createReducer, createAction } from "typesafe-actions";
import { takeEvery, put } from "redux-saga/effects";
import { requestPort } from './webusb';
export const actions = {
    enumerate: createAsyncAction("@usb/enumerate/request", "@usb/enumerate/success", "@usb/enumerate/failed")(),
    disconnect: createAction("@usb/disconnect")(),
    connect: createAction("@usb/connect")(),
    connected: createAction("@usb/connected")(),
    disconnected: createAction("@usb/disconnected")(),
    workerlog: createAction("@usb/worker/log")(),
    workerRequest: createAction("@usb/worker/request")(),
    adc: createAction("@usb/adc")(),
    rawCommand: createAction("@usb/raw/command")(),
};
export default createReducer({})
    .handleAction(actions.enumerate.success, (state, action) => {
    return Object.assign(Object.assign({}, state), { deviceFilter: action.payload });
})
    .handleAction(actions.rawCommand, (state, action) => {
    return Object.assign(Object.assign({}, state), { rawCommand: action.payload });
})
    .handleAction(actions.connected, (state, action) => {
    return Object.assign(Object.assign({}, state), { connected: true });
})
    .handleAction(actions.disconnected, (state, action) => {
    return Object.assign(Object.assign({}, state), { connected: false });
})
    .handleAction(actions.adc, (state, action) => {
    return Object.assign(Object.assign({}, state), { adc: action.payload });
});
export function* saga() {
    yield takeEvery(actions.disconnect, function* (action) {
        yield put(actions.workerRequest({ action: 'close-port' }));
    });
    yield takeEvery(actions.connected, function* (action) {
        yield put(actions.workerRequest({ action: 'raw', value: '!init' }));
    });
    yield takeEvery(actions.connect, function* (action) {
        yield put(actions.workerRequest({ action: 'open-port', filter: action.payload }));
    });
    yield takeEvery(actions.enumerate.request, function* (action) {
        try {
            const port = yield requestPort();
            yield put(actions.workerRequest({ action: 'open-port', filter: port.filter }));
            yield put(actions.enumerate.success(port.filter));
        }
        catch (e) {
            yield put(actions.enumerate.failure(e));
        }
    });
    // yield takeEvery(actions.workerlog, function* (action) {
    //   console.log(action.payload)
    // })
    // yield takeEvery(actions.adc, function* (action) {
    //   console.log(action.payload)
    // })
}
