//split array to array of arrays with max size = chunkSize
export function chunk(inputArr, chunkSize) {
    return inputArr.reduce((prev, one, index) => {
        const chunkIndex = Math.floor(index / chunkSize);
        if (!prev[chunkIndex]) {
            prev[chunkIndex] = []; // start a new chunk
        }
        prev[chunkIndex].push(one);
        return prev;
    }, []);
}
